"use client";

import Link from "next/link";
import { useRouter } from "next/navigation";
import { useState, useEffect } from "react";
import {
  ArrowRight,
  CheckCircle2,
  Clock,
  MapPin,
  Plane,
  AlertTriangle,
  Info,
  Navigation,
} from "lucide-react";
import { usePassenger } from "@/lib/context/PassengerContext";
import { getApplicableSteps, PassengerClass } from "@/lib/types/passenger";
import { passengerClassColors, theme } from "@/lib/config/theme";
import ClassSelectorModal from "@/components/ui/ClassSelectorModal";
import OnboardingTutorial from "@/components/ui/OnboardingTutorial";

export default function Home() {
  const router = useRouter();
  const { profile, setProfile } = usePassenger();
  const [showModal, setShowModal] = useState(false);
  const [showOnboarding, setShowOnboarding] = useState(false);

  useEffect(() => {
    // Show modal on first load if no profile
    if (!profile) {
      setShowModal(true);
    }
  }, [profile]);

  const handleClassSelect = (
    selectedClass: PassengerClass,
    flightNumber?: string,
    gate?: string
  ) => {
    setProfile({
      class: selectedClass,
      flightNumber,
      gate,
      terminal: "1",
    });
    setShowModal(false);
  };

  const handleFirstTimeSelect = () => {
    // Check if user has already seen onboarding
    const hasSeenOnboarding = localStorage.getItem("hasSeenOnboarding");
    if (!hasSeenOnboarding) {
      // Small delay to let modal close smoothly
      setTimeout(() => {
        setShowOnboarding(true);
      }, 300);
    }
  };

  const handleOnboardingComplete = () => {
    setShowOnboarding(false);
    localStorage.setItem("hasSeenOnboarding", "true");
  };

  const steps = profile ? getApplicableSteps(profile.class) : [];
  const currentStepIndex = profile?.currentStep || 0;
  const classColor = profile ? passengerClassColors[profile.class] : null;

  const classLabels = {
    economy: "√âconomique",
    business: "Business",
    first: "Premi√®re Classe",
  };

  const quickActions = [
    {
      href: "/map",
      icon: MapPin,
      title: "Plan de l'a√©roport",
      description: "Explorez la carte interactive",
      color: theme.colors.info.main,
    },
    {
      href: "/navigation",
      icon: Navigation,
      title: "Navigation",
      description: "Trouvez votre chemin",
      color: theme.colors.accent.main,
    },
    {
      href: "/services",
      icon: Info,
      title: "Services",
      description: "Informations et assistance",
      color: theme.colors.success.main,
    },
    {
      href: "/report",
      icon: AlertTriangle,
      title: "Signalement",
      description: "Signalez un probl√®me",
      color: theme.colors.error.main,
    },
  ];

  return (
    <>
      <ClassSelectorModal
        isOpen={showModal}
        onClose={() => {}}
        onSelect={handleClassSelect}
        onFirstTimeSelect={handleFirstTimeSelect}
      />

      <OnboardingTutorial
        show={showOnboarding}
        onComplete={handleOnboardingComplete}
      />

      <div
        className="min-h-screen"
        style={{
          backgroundColor: theme.colors.background.default,
        }}
      >
        <div className="max-w-7xl mx-auto px-4 py-8" style={{ paddingBottom: theme.spacing[20] }}>
          {/* Welcome Section */}
          <div className="mb-12">
            <h1
              className="font-bold mb-2"
              style={{
                fontSize: theme.typography.display.fontSize,
                fontWeight: theme.typography.display.fontWeight,
                lineHeight: theme.typography.display.lineHeight,
                color: theme.colors.text.primary,
              }}
            >
              Bienvenue √† l'a√©roport de Lom√©
            </h1>
            <p
              style={{
                fontSize: theme.typography.large.fontSize,
                lineHeight: theme.typography.large.lineHeight,
                color: theme.colors.text.secondary,
              }}
            >
              Votre parcours simplifi√© de l'arriv√©e √† l'embarquement
            </p>
          </div>

          {profile && (
            <>
              {/* Active Profile Card */}
              <div
                className="rounded-xl p-6 mb-8"
                style={{
                  border: `2px solid ${classColor?.primary}`,
                  backgroundColor: classColor?.background,
                  boxShadow: theme.shadow.md,
                }}
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center space-x-3 mb-3">
                      <div
                        className="w-12 h-12 rounded-full flex items-center justify-center"
                        style={{
                          backgroundColor: classColor?.primary,
                          boxShadow: theme.shadow.sm,
                        }}
                      >
                        <Plane className="w-6 h-6 text-white" />
                      </div>
                      <div>
                        <h3
                          className="font-semibold"
                          style={{
                            fontSize: theme.typography.h5.fontSize,
                            fontWeight: theme.typography.h5.fontWeight,
                            color: classColor?.primary,
                          }}
                        >
                          {classLabels[profile.class]}
                        </h3>
                        {profile.flightNumber && (
                          <p
                            style={{
                              fontSize: theme.typography.base.fontSize,
                              color: theme.colors.text.secondary,
                            }}
                          >
                            Vol {profile.flightNumber}
                          </p>
                        )}
                      </div>
                    </div>
                    {profile.gate && (
                      <div
                        className="flex items-center space-x-2"
                        style={{
                          fontSize: theme.typography.small.fontSize,
                          color: theme.colors.text.secondary,
                        }}
                      >
                        <MapPin className="w-4 h-4" />
                        <span>Porte {profile.gate}</span>
                      </div>
                    )}
                  </div>
                  <button
                    onClick={() => router.push("/profile")}
                    className="rounded-lg transition-all"
                    style={{
                      padding: `${theme.spacing[2]} ${theme.spacing[4]}`,
                      border: `1px solid ${classColor?.primary}`,
                      color: classColor?.primary,
                      fontSize: theme.typography.small.fontSize,
                      fontWeight: 500,
                      backgroundColor: "transparent",
                    }}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.backgroundColor = classColor?.background || "";
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.backgroundColor = "transparent";
                    }}
                  >
                    Modifier
                  </button>
                </div>
              </div>

              {/* Journey Steps */}
              <div
                className="rounded-xl p-6 mb-8"
                style={{
                  backgroundColor: theme.colors.background.elevated,
                  border: `1px solid ${theme.colors.border.main}`,
                  boxShadow: theme.shadow.DEFAULT,
                }}
              >
                <h2
                  className="mb-6 font-semibold"
                  style={{
                    fontSize: theme.typography.h3.fontSize,
                    fontWeight: theme.typography.h3.fontWeight,
                    color: theme.colors.text.primary,
                  }}
                >
                  Votre parcours
                </h2>

                <div className="space-y-4">
                  {steps.map((step, index) => {
                    const isCompleted = index < currentStepIndex;
                    const isCurrent = index === currentStepIndex;
                    const isPending = index > currentStepIndex;

                    return (
                      <div
                        key={step.id}
                        className="relative p-4 rounded-lg transition-all"
                        style={{
                          border: `2px solid ${
                            isCurrent
                              ? theme.colors.accent.main
                              : isCompleted
                              ? theme.colors.success.main
                              : theme.colors.border.main
                          }`,
                          backgroundColor: isCurrent
                            ? theme.colors.accent.main + "10"
                            : isCompleted
                            ? theme.colors.success.main + "10"
                            : theme.colors.background.default,
                        }}
                      >
                        <div className="flex items-start space-x-4">
                          {/* Step Number/Status */}
                          <div
                            className="w-10 h-10 rounded-full flex items-center justify-center flex-shrink-0"
                            style={{
                              backgroundColor: isCompleted
                                ? theme.colors.success.main
                                : isCurrent
                                ? theme.colors.accent.main
                                : theme.colors.gray[300],
                            }}
                          >
                            {isCompleted ? (
                              <CheckCircle2 className="w-6 h-6 text-white" />
                            ) : (
                              <span className="text-white font-semibold">{index + 1}</span>
                            )}
                          </div>

                          {/* Step Content */}
                          <div className="flex-1">
                            <div className="flex items-start justify-between mb-2">
                              <div>
                                <h3
                                  className="font-semibold"
                                  style={{
                                    fontSize: theme.typography.h6.fontSize,
                                    fontWeight: theme.typography.h6.fontWeight,
                                    color: isCurrent
                                      ? theme.colors.accent.dark
                                      : theme.colors.text.primary,
                                  }}
                                >
                                  {step.title}
                                </h3>
                                <p
                                  style={{
                                    fontSize: theme.typography.small.fontSize,
                                    color: isCurrent
                                      ? theme.colors.accent.dark
                                      : theme.colors.text.secondary,
                                  }}
                                >
                                  {step.description}
                                </p>
                              </div>
                              {step.estimatedTime > 0 && (
                                <div
                                  className="flex items-center space-x-1"
                                  style={{
                                    fontSize: theme.typography.small.fontSize,
                                    color: theme.colors.text.secondary,
                                  }}
                                >
                                  <Clock className="w-4 h-4" />
                                  <span>{step.estimatedTime} min</span>
                                </div>
                              )}
                            </div>

                            {/* Step Details */}
                            {isCurrent && step.details && (
                              <ul className="mt-3 space-y-1">
                                {step.details.map((detail, idx) => (
                                  <li
                                    key={idx}
                                    className="flex items-start space-x-2"
                                    style={{
                                      fontSize: theme.typography.small.fontSize,
                                      color: theme.colors.text.secondary,
                                    }}
                                  >
                                    <span style={{ color: theme.colors.accent.main }}>‚Ä¢</span>
                                    <span>{detail}</span>
                                  </li>
                                ))}
                              </ul>
                            )}

                            {/* Action Button */}
                            {isCurrent && (
                              <Link
                                href="/navigation"
                                className="inline-flex items-center space-x-2 mt-4 font-medium transition-colors"
                                style={{
                                  fontSize: theme.typography.small.fontSize,
                                  color: theme.colors.accent.main,
                                }}
                              >
                                <MapPin className="w-4 h-4" />
                                <span>Voir sur la carte</span>
                                <ArrowRight className="w-4 h-4" />
                              </Link>
                            )}
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            </>
          )}

          {/* Quick Actions */}
          <div>
            <h2
              className="mb-6 font-semibold"
              style={{
                fontSize: theme.typography.h4.fontSize,
                fontWeight: theme.typography.h4.fontWeight,
                color: theme.colors.text.primary,
              }}
            >
              Acc√®s rapide
            </h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              {quickActions.map((action) => {
                const Icon = action.icon;
                return (
                  <Link
                    key={action.href}
                    href={action.href}
                    className="rounded-xl p-6 transition-all"
                    style={{
                      backgroundColor: theme.colors.background.elevated,
                      border: `1px solid ${theme.colors.border.main}`,
                      boxShadow: theme.shadow.sm,
                    }}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.boxShadow = theme.shadow.lg;
                      e.currentTarget.style.transform = "translateY(-2px)";
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.boxShadow = theme.shadow.sm;
                      e.currentTarget.style.transform = "translateY(0)";
                    }}
                  >
                    <Icon
                      className="w-8 h-8 mb-4"
                      style={{ color: action.color }}
                    />
                    <h3
                      className="font-semibold mb-1"
                      style={{
                        fontSize: theme.typography.base.fontSize,
                        fontWeight: 600,
                        color: theme.colors.text.primary,
                      }}
                    >
                      {action.title}
                    </h3>
                    <p
                      style={{
                        fontSize: theme.typography.small.fontSize,
                        color: theme.colors.text.secondary,
                      }}
                    >
                      {action.description}
                    </p>
                  </Link>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
